import { React, useState } from "react";
import styles from "./faqWrite.module.css";
import axios from "axios";
import { useHistory } from "react-router";

const FaqWrite = () => {
  const history = useHistory();
  const [inputValues, setInputValues] = useState({
    title: "",
    content: "",
    type: "",
  });
  const { title, content, type } = inputValues;

  const inputValueChangeHandler = (e) => {
    const { name, value } = e.target;
    setInputValues({
      ...inputValues,
      [name]: value,
    });
  };

  const writeSubmitHandler = (e) => {
    e.preventDefault();

    if (title === "" || content === "") {
      window.alert("  ");
      return;
    }

    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/session`)
      .then((response) => {
        if (response.data === "" || response.data.idType !== 2) {
          window.alert("  .");
          return;
        }

        axios
          .post(`${process.env.REACT_APP_BASEURL}/faq/insertfaq`, {
            type,
            title,
            content,
          })
          .then((response) => {
            history.push("/customer/faq");
            window.scrollTo({ top: 0 });
          })
          .catch((err) => console.error(err));
      })
      .catch((err) => console.error(err));
  };
  return (
    <section className={styles.write}>
      <form className={styles.write_form}>
        <div className={styles.type_select_container}>
          <p className={styles.type_text}></p>
          <select
            name="type"
            onChange={inputValueChangeHandler}
            value={type}
            className={styles.type_select}
          >
            <option value="" className={styles.type}>
              
            </option>
            <option value="/" className={styles.type}>
              /
            </option>
            <option value="" className={styles.type}>
              
            </option>
            <option value=" " className={styles.type}>
               
            </option>
          </select>
        </div>
        <div className={styles.title_input_container}>
          <p className={styles.title_text}></p>
          <input
            name="title"
            onChange={inputValueChangeHandler}
            value={title}
            type="text"
            className={styles.title_input}
            spellCheck="false"
            placeholder=""
          />
        </div>
        <div className={styles.content_input_container}>
          <p className={styles.content_text}></p>
          <textarea
            name="content"
            onChange={inputValueChangeHandler}
            value={content}
            className={styles.content_input}
            spellCheck="false"
            placeholder=""
          ></textarea>
        </div>
        <button className={styles.submit_button} onClick={writeSubmitHandler}>
          
        </button>
      </form>
    </section>
  );
};

export default FaqWrite;
