import React, { useEffect, useState } from "react";
import styles from "./mypageMain.module.css";
import axios from "axios";

const MypageMain = ({ user, sessionCheck }) => {
  const [inputValues, setInputValues] = useState({
    pw: "",
    newPw: "",
    newPwConfirm: "",
    name: "",
    phone: "",
    authNum: "",
  });

  const { pw, newPw, newPwConfirm, name, phone, authNum } = inputValues;

  const inputValueChangeHandler = (e) => {
    const { name, value } = e.target;
    setInputValues({
      ...inputValues,
      [name]: value,
    });
  };

  const onPasswordChangeHandler = () => {
    //        
    if (newPw !== newPwConfirm) {
      window.alert("    .");
      return;
    }

    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/modifyinfo`, {
        id: user.id,
        phone: user.phone,
        password: newPw,
      })
      .then((response) => console.log(response))
      .catch((err) => console.error(err));
  };

  const onSmsHandler = () => {
    //
  };

  const onPhoneChangeHandler = () => {
    //        
    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/modifyinfo`, {
        id: user.id,
        phone,
        password: "dww",
      })
      .then((response) => console.log(response))
      .catch((err) => console.error(err));
  };

  useEffect(() => {
    sessionCheck();
    setInputValues({
      ...inputValues,
      name: user.name,
      phone: user.phone,
    });
  }, []);
  return (
    <section className={styles.mypage_main}>
      <div className={styles.top}>
        <img
          src="/modoorock/images/profile_default.png"
          alt="profile"
          className={styles.profile_image}
        />
        <div className={styles.name_and_point}>
          <p className={styles.name}>{`${user.id} `}</p>
          <p className={styles.point}>{` : ${user.totalPoint}`}</p>
        </div>
      </div>
      <div className={styles.main}>
        <div className={styles.input_container}>
          <p className={styles.input_title}></p>
          <input
            name="name"
            onChange={inputValueChangeHandler}
            value={name}
            type="text"
            className={styles.input}
            spellCheck="false"
            placeholder=""
            disabled="disabled"
          />
        </div>
        <div className={styles.input_container}>
          <p className={styles.input_title}></p>
          <input
            name="pw"
            onChange={inputValueChangeHandler}
            value={pw}
            type="password"
            className={styles.input}
            spellCheck="false"
            placeholder=""
          />
          <p className={styles.input_title}> </p>
          <input
            name="newPw"
            onChange={inputValueChangeHandler}
            value={newPw}
            type="password"
            className={styles.input}
            spellCheck="false"
            placeholder=" "
          />
          <p className={styles.input_title}>  </p>

          <div className={styles.button_and_input}>
            <input
              name="newPwConfirm"
              onChange={inputValueChangeHandler}
              value={newPwConfirm}
              type="password"
              className={styles.input}
              spellCheck="false"
              placeholder="  "
            />
            <button className={styles.button}> </button>
          </div>
        </div>

        <div className={styles.input_container}>
          <p className={styles.input_title}></p>

          <div className={styles.button_and_input}>
            <input
              name="phone"
              onChange={inputValueChangeHandler}
              value={phone}
              type="text"
              className={styles.input}
              spellCheck="false"
              placeholder=""
            />
            <button className={styles.button}> </button>
          </div>
        </div>
        <div className={styles.input_container}>
          <p className={styles.input_title}></p>

          <div className={styles.button_and_input}>
            <input
              name="authNum"
              onChange={inputValueChangeHandler}
              value={authNum}
              type="text"
              className={styles.input}
              spellCheck="false"
              placeholder=""
            />
            <button className={styles.button}>  </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default MypageMain;
