import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import LoadingSpinnerWhite from "../loadingSpinner/loadingSpinnerWhite/loadingSpinnerWhite";
import styles from "./loginPage.module.css";

axios.defaults.withCredentials = true;

const LoginPage = () => {
  const history = useHistory();
  const [loadingOn, setLoadingOn] = useState(false);
  const [idSave, setIdSave] = useState(() => {
    if (localStorage.getItem("id")) {
      return true;
    }
    return false;
  });

  useEffect(() => {
    const id = localStorage.getItem("id");
    if (id) {
      setInputValues({
        ...inputValues,
        id,
      });
    }
  }, []);

  const [inputValues, setInputValues] = useState({
    id: "",
    pw: "",
  });

  const { id, pw } = inputValues;

  const idSaveHandler = (e) => {
    setIdSave(!idSave);
  };

  const inputValueChangeHandler = (e) => {
    const { name, value } = e.target;
    setInputValues({
      ...inputValues,
      [name]: value,
    });
  };

  const refresh = () => {
    window.location.href = "/modoorock";
    return;
  };

  const loginSubmitHandler = async (e) => {
    e.preventDefault();

    if (id === "" || pw === "") {
      window.alert("  ");
      return;
    }

    setLoadingOn(true);

    await axios
      .post(
        `${process.env.REACT_APP_BASEURL}/user/login`,
        {
          id,
          password: pw,
        },
        { withCredentials: true }
      )
      .then((response) => {
        if (response.data === "loggedin") {
          idSave && localStorage.setItem("id", id);
          !idSave && localStorage.removeItem("id");
          window.alert(" .");
          refresh();
        } else {
          window.alert("   ");
        }
        setLoadingOn(false);
      })
      .catch((err) => {
        setLoadingOn(false);
        console.error(err);
      });
  };

  return (
    <section className={styles.login_page}>
      <section className={styles.container}>
        <p className={styles.title}></p>
        <form className={styles.main}>
          <div className={styles.input_container}>
            <input
              name="id"
              onChange={inputValueChangeHandler}
              value={id}
              type="text"
              className={styles.input}
              placeholder=""
              spellCheck="false"
            />
            <input
              name="pw"
              onChange={inputValueChangeHandler}
              value={pw}
              type="password"
              className={styles.input}
              placeholder=""
              spellCheck="false"
            />
          </div>
          <button className={styles.submit_button} onClick={loginSubmitHandler}>
            {loadingOn ? <LoadingSpinnerWhite /> : ""}
          </button>
        </form>
        <div className={styles.id_save_find_container}>
          <div className={styles.id_save_container}>
            <input
              type="checkbox"
              checked={idSave}
              onChange={idSaveHandler}
              className={styles.id_save_checkbox}
            />
            <span className={styles.id_save_text}> </span>
          </div>
          <div className={styles.id_pw_find_container}>
            <button
              className={`${styles.id_pw_find_button} ${styles.left}`}
              onClick={() => {
                history.push("/find");
              }}
            >
              
            </button>
            |
            <button
              className={styles.id_pw_find_button}
              onClick={() => {
                history.push("/find");
              }}
            >
              
            </button>
          </div>
        </div>
        <div className={styles.sns_login_container}>
           SNS 
        </div>
        <div className={styles.signup_container}>
          <span className={styles.signup_message}>
                  .
          </span>
          <button
            className={styles.go_signup_button}
            onClick={() => {
              history.push("/signup");
              window.scrollTo({ top: 0 });
            }}
          >
            
          </button>
        </div>
      </section>
    </section>
  );
};

export default LoginPage;
