import axios from "axios";
import React, { useEffect, useState } from "react";
import styles from "./adminProgramMissionItem.module.css";

//    ?
// mission_type_and_name = 
const AdminProgramMissionItem = ({ item, setMissionLoaderHandler }) => {
  const [data, setData] = useState(null);
  const [typeName, setTypeName] = useState(null);

  const onDeleteHandler = (e) => {
    if (!data) {
      window.alert("   ");
      return;
    }
    e.stopPropagation();
    const confirm = window.confirm(" ?");
    if (!confirm) {
      return;
    }
    axios
      .post(`${process.env.REACT_APP_BASEURL}/mission/deletemission`, {
        missionIdx: data.missionIdx,
      })
      .then((response) => {
        if (response.data === "success") {
          setMissionLoaderHandler();
          window.alert(".");
        } else {
          window.alert(" .    ");
        }
      })
      .catch((err) => console.error(err));
  };

  useEffect(() => {
    const getMissionData = () => {
      axios
        .post(`${process.env.REACT_APP_BASEURL}/mission/getmission`, {
          idx: item.idx,
          typeIdx: item.typeIdx,
        })
        .then((response) => setData(response.data))
        .catch((err) => console.error(err));
    };

    const getTypeName = () => {
      switch (item.typeIdx) {
        case 1:
          setTypeName("");
          break;
        case 2:
          setTypeName("OX");
          break;
        case 3:
          setTypeName("4");
          break;
        case 4:
          setTypeName("");
          break;
        case 5:
          setTypeName("");
          break;
        case 6:
          setTypeName("");
          break;
        case 7:
          setTypeName("");
          break;
        case 8:
          setTypeName("");
          break;
        case 9:
          setTypeName("");
          break;
        default:
      }
    };
    getMissionData();
    getTypeName();
  }, []);

  return (
    <div className={styles.mission_item}>
      <div className={styles.mission_top}>
        <div className={styles.division}></div>
        <p className={styles.mission_type_and_name}>
          {typeName && data && `${typeName} - ${data.title}`}
        </p>
        <button className={styles.add_mission_button}>
          <i className={`${styles.add_button_icon} fas fa-edit`}></i>
        </button>
        <button
          className={styles.delete_mission_button}
          onClick={onDeleteHandler}
        >
          <i className={`${styles.delete_button_icon} fas fa-times`}></i>
        </button>
      </div>
    </div>
  );
};

export default AdminProgramMissionItem;
