import React, { useState } from "react";
import styles from "./findPw.module.css";
import axios from "axios";
import { useHistory } from "react-router";

const FindPw = (props) => {
  const history = useHistory();
  const [checkedPhone, setCheckedPhone] = useState(null);
  const [disable, setDisable] = useState(false);
  const [inputValues, setInputValues] = useState({
    id: "",
    phone: "",
    authNum: "",
    newPw: "",
    newPwConfirm: "",
  });

  const { id, phone, authNum, newPw, newPwConfirm } = inputValues;

  const inputValueChangeHandler = (e) => {
    const { name, value } = e.target;
    setInputValues({
      ...inputValues,
      [name]: value,
    });
  };

  const sendSmsHandler = () => {
    if (phone === "") {
      window.alert("   ");
      return;
    }
    if (phone.length !== 11) {
      window.alert("   ");
      return;
    }
    for (let i = 0; i < phone.length; i++) {
      if (isNaN(parseInt(phone.charAt(i)))) {
        window.alert(" ");
        return;
      }
    }

    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/userphonevalid`, {
        phone,
      })
      .then((response) => {
        if (response.data === "OK") {
          window.alert("    .");
          return;
        }
        axios
          .post(`${process.env.REACT_APP_BASEURL}/user/requestsms`, {
            phone,
          })
          .then((response) => window.alert(" ."))
          .catch((err) => console.error(err));
      })
      .catch((err) => console.error(err));
  };

  const authNumCheckHandler = () => {
    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/checksms`, {
        to: phone,
        content: authNum,
      })
      .then((response) => {
        if (response.data === "success") {
          window.alert(" .");
          setCheckedPhone(phone);
          setDisable(true);
          return;
        }
        window.alert(" .");
      });
  };

  const findPwHandler = (e) => {
    e.preventDefault();
    if (newPw !== newPwConfirm) {
      window.alert("    .");
      return;
    }
    if (newPw.length < 8 || newPw.length > 16) {
      window.alert(" 8~16 .");
      return;
    }
    axios
      .post(`${process.env.REACT_APP_BASEURL}/user/findpassword`, {
        id,
        phone: checkedPhone,
        password: newPw,
      })
      .then((response) => {
        if (response.data === "success") {
          window.alert("  .");
          history.push("/");
        } else {
          window.alert("  .");
        }
      })
      .catch((err) => console.error(err));
  };

  return (
    <section className={styles.find_pw}>
      <form className={styles.find_pw_form}>
        <p className={styles.find_pw_title}> </p>
        <div className={styles.main_container}>
          <div className={styles.id_input_container}>
            <p className={styles.id_input_text}></p>
            <input
              name="id"
              onChange={inputValueChangeHandler}
              value={id}
              type="text"
              className={styles.id_input}
              placeholder=""
              spellCheck="false"
            />
          </div>

          <div className={styles.phone_num_container}>
            <p className={styles.phone_num_text}></p>

            <input
              name="phone"
              onChange={inputValueChangeHandler}
              value={phone}
              type="text"
              className={styles.phone_num_input}
              placeholder=""
              spellCheck="false"
              disabled={disable}
            />
            <button
              type="button"
              className={styles.get_auth_num_button}
              onClick={sendSmsHandler}
            >
               
            </button>
          </div>

          <div className={styles.auth_num_container}>
            <p className={styles.auth_num_text}></p>

            <input
              name="authNum"
              onChange={inputValueChangeHandler}
              value={authNum}
              type="text"
              className={styles.auth_num_input}
              placeholder=""
              spellCheck="false"
              disabled={disable}
            />
            <button
              type="button"
              className={styles.confirm_auth_num_button}
              onClick={authNumCheckHandler}
            >
              
            </button>
          </div>
          {checkedPhone && (
            <div>
              <div className={styles.new_pw_container}>
                <p className={styles.auth_num_text}> </p>

                <input
                  name="newPw"
                  onChange={inputValueChangeHandler}
                  value={newPw}
                  type="password"
                  className={styles.auth_num_input}
                  placeholder="  (8~16)"
                  spellCheck="false"
                />
              </div>
              <div className={styles.new_pw_confirm_container}>
                <p className={styles.auth_num_text}>  </p>

                <input
                  name="newPwConfirm"
                  onChange={inputValueChangeHandler}
                  value={newPwConfirm}
                  type="password"
                  className={styles.auth_num_input}
                  placeholder="   (8~16)"
                  spellCheck="false"
                />
              </div>
            </div>
          )}
        </div>

        <div className={styles.submit_button_container}>
          <button className={styles.submit_button} onClick={findPwHandler}>
             
          </button>
        </div>
      </form>
    </section>
  );
};

export default FindPw;
